/* Food.css */

.menu {
  padding: 20px;
  background-color: #f8f8f8;
}

.menu-heading {
  margin-top: 100px;
}

.menu h1 {
  text-align: center;
  font-size: 2.5em;
  margin-bottom: 20px;
  color: #333;
}

.menu-heading2{
  font-size: 1.9em;
  text-align: center;
  color: #333;
}

.menu-section {
  margin-bottom: 30px;
}

.menu-section h2 {
  font-size: 1.8em;
  margin-bottom: 15px;
  color: #555;
  border-bottom: 2px solid #ccc;
  padding-bottom: 5px;
}

.menu-items {
  list-style-type: none;
  padding: 0;
}

.item-details {
  display: flex;
  flex-direction: column;
}

.item-name {
  font-weight: bold;
  color: #333;
  flex: 1;
}

.item-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.item-description {
  margin-right: auto;
  color: #777;
  font-size: 0.9em;
}

.item-price {
  color: #777;
  font-weight: bold;
  white-space: nowrap;
}

.menu-item {
  font-size: 1.2em;
  margin-bottom: 10px;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.menu-item p {
  margin: 5px 0 0 10px;
  font-size: 0.9em;
  color: #777;
}

@media screen and (max-width: 768px) {
  .menu-heading {
    margin-top: 100px;
  }

  .menu h1 {
    font-size: 2em;
  }

  .menu-heading2{
    font-size: 1.6em;
    text-align: center;
    color: #333;
  }

  .menu-section h2 {
    font-size: 1.5em;
  }

  .item-details {
    flex-direction: column;
    align-items: flex-start;
  }

  .item-name,
  .item-info {
    font-size: 1em;
  }

  .menu-item {
    font-size: 1em;
  }

  .menu-item p {
    margin: 5px 0 0;
  }
}

@media screen and (max-width: 576px) {
  .menu-heading {
    margin-top: 100px;
  }
  .menu-heading2{
    font-size: 1.3em;
    text-align: center;
    color: #333;
  }
  .menu h1 {
    font-size: 1.8em;
  }

  .menu-section h2 {
    font-size: 1.2em;
  }

  .item-details {
    flex-direction: column;
    align-items: flex-start;
  }

  .item-name,
  .item-info {
    font-size: 0.9em;
    display: flex;
    justify-content: space-between;
    padding: 3px;
  }

  .menu-item {
    font-size: 0.9em;
  }

  .menu-item p {
    font-size: 0.8em;
  }
}
